<template>
  <div class="row" style="padding:0; margin:0; width: 100%; position: relative; ">

    <router-view></router-view>
    <Content-SideBar @modal="onModalRequest"></Content-SideBar>
    <div style="z-index: 5000">
      <el-dialog v-model="modal.tou" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <!--<h5><b>Jump To Sections:</b></h5>
            <br/>
            <div class="section">
              <ul>
                <li><a href="#"><h6><b>Commercial Use of Service</b></h6></a></li>
                <li><a href="#"><h6><b>Account Security</b></h6></a></li>
                <li><a href="#"><h6><b>Term</b></h6></a></li>
                <li><a href="#"><h6><b>Content Ownership</b></h6></a></li>
                <li><a href="#"><h6><b>Restrictions on Use</b></h6></a></li>
                <li><a href="#"><h6><b>Copyright Policy</b></h6></a></li>
                <li><a href="#"><h6><b>Liability for Content</b></h6></a></li>
                <li><a href="#"><h6><b>Privacy</b></h6></a></li>
              </ul>
            </div>
            <br/>-->
            <div class="modal-body">
              <div class="caption">
                <tos></tos>
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>
      <el-dialog v-model="modal.contact" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <h5><b>Contact Us</b></h5>
            <div class="modal-body">
              <div class="caption">
              </div>
              <div class="row">
                <div class="col-lg-6 col-md-6 col-sm-12 reset-col">
                  <label for="contactUserName">Your Name: <span style="color: #e53935;">*</span></label>
                  <el-input type="text" v-model="contactUserName" data-vv-as="name" v-validate:contactUserName="'required'" name="contactUserName" id="contactUserName" placeholder="Your name"></el-input>
                </div>
                <div class="a-row" style="min-height:15px;">
                  <p v-show="errors.has('contactUserName')" class="help is-danger">{{ errors.first('contactUserName') }}</p>
                </div>
              </div>
              <div class="a-row">
                <div class="col-lg-6 col-md-6 col-sm-12 reset-col">
                  <label for="contactUserEmail">Your Email: <span style="color: #e53935;">*</span></label>
                  <el-input type="text" v-model="contactUserEmail" data-vv-as="email" id="contactUserEmail"
                            placeholder="Your Email" v-validate:contactUserEmail="'required'" name="contactUserEmail"></el-input>
                </div>
                <div class="a-row" style="min-height:15px;">
                  <p v-show="errors.has('contactUserEmail')" class="help is-danger">{{ errors.first('contactUserEmail') }}</p>
                </div>
              </div>
              <div class="a-row">
                <div class="col-lg-6 col-md-6 col-sm-12 reset-col">
                  <label for="contactSubject">Subject: <span style="color: #e53935;">*</span></label>
                  <el-input type="text" v-model="contactSubject" id="contactSubject"
                            placeholder="Subject Title" data-vv-as="subject" v-validate:contactSubject="'required'" name="contactSubject"></el-input>
                </div>
                <div class="a-row" style="min-height:15px;">
                  <p v-show="errors.has('contactSubject')" class="help is-danger">{{ errors.first('contactSubject') }}</p>
                </div>
              </div>
              <div class="a-row">
                <div class="col-lg-12 reset-col">
                  <label for="contactMessage">Message: <span style="color: #e53935;">*</span></label>
                  <el-input type="textarea" v-model="contactMessage" id="contactMessage"
                            placeholder="Your Message" data-vv-as="message" v-validate:contactMessage="'required'" name="contactMessage"></el-input>
                </div>
                <div class="a-row" style="min-height:15px;">
                  <p v-show="errors.has('contactMessage')" class="help is-danger">{{ errors.first('contactMessage') }}</p>
                </div>
              </div>
              <div class="a-row" style="margin-top:10px;">
                <vue-recaptcha ref="recap" @verify="onVerify" :sitekey="key">
                </vue-recaptcha>
                <div class="a-row" style="min-height:15px;">
                  <p v-show="errors.has('reResponse')" class="help is-danger">{{ errors.first('reResponse') }}</p>
                </div>
                <el-button type="primary" @click="sendMessage">Send Message</el-button>
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>

      <el-dialog v-model="modal.faq" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <h5><b>FAQ</b></h5>
            <div class="modal-body">
              <div class="caption">
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>


      <el-dialog v-model="modal.about" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain" style=" padding-bottom:50px;">
            <div class="modal-body" style="min-height:250px;">
              <p class="text-center" style="font-size:25px;"><b>About Us</b></p>
              <div style="padding-left:75px; padding-right:75px;">
                <p>We are a small company comprised of  individuals with different backgrounds.  The one thing that unites us is our never ending quest to save money - for us, for you and for all the shoppers out there.  Internet is a vast place - there are so many sites to shop from, and so many product options. It is not uncommon for people to spend hours looking for the best price of products they want to buy. </p>
                <p style="margin-bottom:5px;">Our mission is to deliver the best bargains to you. We deliver bargains to you in a few different ways:</p>
                <ul style="padding-left:35px;">
                  <li>Editor’s choice bargain: hand-picked and examined thoroughly by our staff before they are posted</li>
                  <li>Store bargains: Streamed straight from top stores we all love.</li>
                </ul>
                <p style="margin-bottom:0;">We update our bargains regularly throughout the day. Check back often as some of these bargains have a short shelf life, as they may be rare. While you are on the site, discuss bargains with fellow bargain hunters, and post bargains we may have missed. </p>
                <p>Please contact us if you have any questions or comments.</p>
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>

      <el-dialog v-model="modal.disclaimer" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <h5><b>Disclaimer</b></h5>
            <div class="modal-body">
              <div class="caption">
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>


      <el-dialog v-model="modal.affiliate" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <h4><b>Affiliate Disclosures</b></h4>
            <div class="modal-body">
              <div class="caption">
                <p>The owner of this website is required by the FTC to inform you that a relationship exists between Best Bargains, Inc. and some of the products or services it recommends, reviews or promotes.</p>
                <p>Best Bargains, Inc. makes use of affiliate relationships with merchants for monetization. This means when you click on links to various products or merchants on this site and make a purchase, this can result in a commission that is credited to this site. Some affiliate programs and affiliations include, but are not limited to, Commission Junction, Rakuten Marketing.</p>
                <p>A product’s price is not affected by an affiliate link. Whether you purchase the product through an affiliate link, or directly on the merchant’s site (without using the affiliate link), the price remains the same. In general, affiliate links do not affect you in any way.</p>
                <br>
                <h5><b>Amazon-specific Affiliate Disclosures:</b></h5>
                <p><b>Participation</b><br />Best Bargains, Inc. is also a participant in the Amazon Services LLC Associates Program, an affiliate advertising program designed to provide a means for sites to earn advertising fees by advertising and linking to amazon.com.</p>
                <p><b>Trademarks</b><br />Amazon, the Amazon logo, AmazonSupply, and the AmazonSupply logo are trademarks of Amazon.com, Inc. or their affiliates.</p>
                <p><b>Pricing and Availability</b><br />Product prices and availability are accurate as of the date/time indicated and are subject to change. Any price and availability information displayed on Amazon's site at the time of purchase will apply to the purchase of this product.</p>
                <p><b>DISCLAIMER</b><br />CERTAIN CONTENT THAT APPEARS ON THIS SITE COMES FROM AMAZON SERVICES LLC. THIS CONTENT IS PROVIDED &lsquo;AS IS' AND IS SUBJECT TO CHANGE OR REMOVAL AT ANY TIME.</p>
                <br>
                <h5><b>Third-Party & Sponsor Ads:</b></h5>
                <p>Text and image ads that appear on best.bargains are provided by third party ad agencies such as Google Adsense. We do not endorse the products and services advertised in these ads. These companies are not affiliated with Best Bargains, Inc.</p>
                <br>
                <p>If you have any questions regarding the above, please do not hesitate to contact us by using the Contact Us page.</p>
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>

      <el-dialog v-model="modal.privacy" size="small" :show-close="false" :top="top" @close="onClose">
        <div slot="title">
          <div class="el-dialog__headerbtn">
            <router-link :to="{name: getRoute, query: {}}" v-on:click.native="closeModal"><i
              class="el-dialog__close el-icon el-icon-close"></i></router-link>
          </div>
        </div>
        <slot>
          <div class="container add-bargain">
            <div class="modal-body">
              <div class="caption">
                <privacy></privacy>
              </div>
            </div>
          </div>
        </slot>
      </el-dialog>
    </div>
  </div>
</template>

<style>
  .el-textarea__inner {
    min-height: 100px;
  }

  div.el-dialog--small {
    width: 100%;
  }

  div.img-card.col-sm-2 {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 16.666667%;
    -ms-flex: 0 0 16.666667%;
    flex: 0 0 16.666667%;
    max-width: 16.666667%;
  }

  div.card-block.col-sm-10 {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 83.333333%;
    -ms-flex: 0 0 83.333333%;
    flex: 0 0 83.333333%;
    max-width: 83.333333%;
  }

  @media (min-width: 0px) {
    div.el-dialog--small {
      top: 0 !important;
    }
  }

  @media (min-width: 700px) {
    div.el-dialog__body .container {
      width: 700px;
    }
  }

  @media (min-width: 800px) {
    div.el-dialog__body .container {
      width: 800px;
    }
  }

  @media (min-width: 825px) {
    div.el-dialog--small {
      width: 825px;
      top: 2% !important;
    }
  }

  @media (min-width: 1000px) {
    div.el-dialog__body .container {
      width: 950px;
    }

    div.el-dialog--small {
      width: 1000px;
      top: 2% !important;
    }
  }
</style>

<script>
  import ContentSideBar from './side-bar.vue'
  import privacy from './ToS/privacy.vue'
  import tos from './ToS/tos.vue'
  import _ from 'lodash'
  import { Message } from 'element-ui'
  import { dataBase } from  '../../firebase/vueFire'
  var ref = dataBase.ref()
  export default {
    name: 'ParentContent',
    data () {
      return {
        top: "3%",
        contactUserName: null,
        contactLoading: false,
        contactUserEmail: null,
        contactMessage: null,
        contactSubject: null,
        key: process.env.RECAPTCHA,
        reResponse: '',
        modal: {
          contact: false,
          faq: false,
          about: false,
          tou: false,
          disclaimer: false,
          privacy: false,
          affiliate: false
        },
        storeAlert: _.isEmpty(localStorage.getItem('storeAlert')) ? true : localStorage.getItem('storeAlert'),
        html: null,
        data: {}
      }
    },
    computed: {
      getHtml () {
        return this.html
      },
      getRoute () {
        return this.$route.name
      }
    },
    methods: {
      onVerify (response) {
        this.reResponse = response
      },
      sendMessage () {
        this.$validator.attach('reResponse', 'required', {
          prettyName: 'Captcha verify',
          context: () => this.reResponse,
          getter: (context) => context
        })
        this.$validator.validateAll().then((result) => {
          this.contactLoading = true
          ref.child('contact-us-messages').child(new Date().getTime()).set({
            name: this.contactUserName,
            from: this.contactUserEmail,
            subject: this.contactSubject,
            message: this.contactMessage
          }).then((response) => {
            Message({
              showClose: true,
              message: "Your message has been successfully sent! We will reach out to you as soon as we can.",
              type: 'success'
            })
            this.contactLoading = true
            this.closeModal()
          }).catch((error) => {
            this.contactLoading = true
            Message({
              showClose: true,
              message: "There was an error when trying to submit your message.",
              type: 'error'
            })
          })
        })
      },
      onModalRequest (data) {
        this.data = data
        this.modal[data.modal] = true
        this.errors.clear()
      },
      resetCaptcha () {
        this.$refs.recap.reset()
      },
      reInitContactForm () {
        this.contactUserName = null
        this.contactUserEmail = null
        this.contactSubject = null
        this.contactMessage = null
      },
      onClose () {
        if (this.$route.query.modal === 'contact') {
          this.resetCaptcha()
        }
        this.reResponse = ''
        this.reInitContactForm()
        this.errors.clear()
        this.modal[this.data.modal] = false
        this.$router.push({name: this.$route.name, query: {}})
      },
      closeModal () {
        this.modal[this.data.modal] = false
        this.errors.clear()
      }
    },
    beforeRouteLeave (to, from, next) {
      $(window).unbind('scroll.sticky-scroll resize.sticky-scroll')
      next()
    },
    mounted () {
    },
    components: {
      ContentSideBar,
      privacy,
      tos
    }
  }
</script>



